import $ from 'jquery';
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import utils from '../../utils';
import initSlider from "../slider";
let { isTouchDevice, onSwipe, Wheel, isMobile, isPortrait } = utils;

function getOffset($elemTop){
    return !isTouchDevice ? $elemTop : ($elemTop - lscroll.scroll.instance.scroll.y)
}

function getZeroToOne(value){
    if(value <= 0) { return 0; }
    if(value >= 1) { return 1; }

    return value;
}

function Horizontal($el, index) {
    this.$wrap = $el;
    this.$scroller = $('.horizontal', $el);
    this.$parallax = $('[data-horizontal-parallax]');
    this.progress = 0;
    this.ignore = 100;

    this.init = function() {

        this.tl = gsap.timeline({paused: true})

        this.scrollLimit = this.$scroller[0].scrollWidth-window.innerWidth + ($(window).width()-$('.container').width()) / 2;
        // 

        this.tl.to(this.$scroller, {
            x:-this.scrollLimit,
            ease: 'none',
        })

        const animationTrigger = ScrollTrigger.create({
            scroller: "[data-scroll-container]",
            trigger: document.querySelector('[data-horizontal]'),
            animation: this.tl,
            scrub: 0.5,
            start: "top top",
            end: "bottom bottom",
            onUpdate: self => {
                // console.log("progress", self.progress)
                // this.parallax();
            },
            pin: false,
            pinSpacing: false,
            markers: false,
        });

        // this.parallax();
        this.attachEvents();
    }

    this.attachEvents = function(){
        var self = this;
        document.addEventListener('ScrollEvent', function(e){
            // self.update();
            self.parallax();
        });
    }

    this.update = function(){
        var self = this;

        var el = this.$wrap[0];
        var elemTop = this.$wrap.offset().top+this.ignore;
        var elemHeight = el.offsetHeight-this.ignore;
        var realHeight = elemHeight - window.innerHeight;
        
        if(elemTop < window.innerHeight*2 && -elemTop < realHeight) {
            var progress = getZeroToOne(-elemTop / (realHeight-100));

            this.progress = progress;
            this.tl.progress(progress);

            this.parallax();
        }
    }

    this.parallax = function(){
        var self = this;

        var x = self.scrollLimit*self.progress;
        var viewPortMiddle = x+window.innerWidth/2;

        this.$parallax.each(function(index, el) {
            var $wrap = $(this).wrap();
            var wrapMiddle = $wrap.offset().left+el.offsetWidth/2+x;

            var delta = (viewPortMiddle - wrapMiddle)*0.15;

            el.style.transform = 'translate3d('+ delta + 'px, 0, 0) scale(1.1)';
        });
    }

    this.initMobile = function() {
        var images = $('img' ,this.$wrap).map(function(i, el){
            return el.dataset.src;
        });

        var items = images.map(function(i, el){
            return `<li>
                        <div class="gallery__img-inner">
                            <div class="gallery__img" data-component="lazyload" data-bg="${el}"></div>
                        </div>
                    </li>`
        }).get().join('');

        var captions = $('.caption' ,this.$wrap).map(function(i, el){
            return `<li class="caption">${el.innerHTML}</li>`;
        }).get().join('');

        // console.log(captions);

        var swiper = `<div class="swiper u-mb110"  data-scroll data-scroll-repeat data-slider data-cursor-arrow>
            <div class="swiper_col swiper_col-1">
                <div class="swiper_outer">
                    <div class="swiper_inner" data-scroll>
                        <ul class="gallery__list gallery-slider">
                            ${items}
                        </ul>
                    </div>
                </div>
            </div>
            <div class="swiper_col swiper_col-2">
                <div class="swiper_outer">
                    <div class="swiper_inner" data-scroll>
                        <ul class="gallery__list gallery-slider">
                            ${items}
                        </ul>
                    </div>
                </div>

                <ul class="captions-list">
                    ${captions}
                </ul>

                <div class="counter window-slider_counter">
                    <div class="counter_number">
                        <span class="counter_current">01</span>
                        <div class="counter_line"><span></span></div>
                        <span class="counter_total">06</span>
                    </div>
                </div>
            </div>
            <div class="slider-cursor"></div>
        </div>`;

        var $swiper = $($.parseHTML(swiper));

        if(isMobile) $('[data-scroll]', $swiper).removeAttr('data-scroll-speed');

        this.$wrap.after($swiper);

        this.$wrap.remove();

        // console.log(swiper);
    }
}

export default function () {
    $('[data-horizontal]').each(function(index, el) {
		var horizontal = new Horizontal($(el), index);
        isPortrait ? horizontal.initMobile() : horizontal.init();

        initSlider();
	});
}