// import { SplitText } from 'gsap/SplitText';
import lottie from "lottie-web";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Snap from "./useSnap";
import utils from '../utils';
import locomotiveScroll from "locomotive-scroll";
let { isTouchDevice, onSwipe, Wheel, isMobile } = utils;

gsap.registerPlugin(ScrollTrigger);

class Hero {
  constructor({holder}) {
    this.holder = holder;
  }

  initAnimation() {
    this.animation = gsap.timeline({ paused: true });
    let totalFrames = this.preloaderAnim.totalFrames - 1;

    let playhead = { frame: this.endFrame };

    this.animation
      .to(document.querySelector(".hero_text-wrap"), {
        y: -700,
        duration: 1,
        ease: "linear",
      },'0')
      .to(document.querySelector('.hero_text-wrap'), {
        opacity:0,
        duration: 0.8,
        ease: 'linear'
      }, '0')
      .to(document.querySelector('.hero_logo'), {
        opacity:0,
        duration: 3,
        ease: 'linear'
      }, '0')
      // .to({},{duration: 10})
      .to(
        playhead,
        {
          frame: totalFrames,
          duration: 10, //2, //preloaderAnim.getDuration(false),
          ease: "none",
          onUpdate: () => this.preloaderAnim.goToAndStop(playhead.frame, true),
        },
        "0"
      );

    const animationTrigger = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: document.querySelector("#hero"),
      animation: this.animation,
      scrub: 1,
      start: "top top",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: false,
      pinSpacing: false,
      markers: false,
    });

    isMobile ? this.initSnapMobile() : this.initSnap();
  }

  initSnap(){
    this.snap = new Snap({
      element: document.querySelector('.hero'),
      goToPoint: (point, duration, index, pointElem) => {
        
      },
      hasPin: false,
      stickOnInit: true
    });

    this.snap.init();
  }

  initSnapMobile() {
    document.querySelector('.hero-video-holder').removeAttribute('data-scroll-sticky');
    $(this.holder).find('.hero-video-holder').addClass('-static');
    lscroll.update();

    const animationTrigger1 = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: document.querySelector('[data-mobile-snap]'),
      scrub: 0,
      start: "top top",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: document.querySelector('.preloader'),
      onLeave: () => {
        gsap.to(document.querySelector('.hero .hero_scroll'), {
          duration:0.3,
          opacity: 0
        })
      },
      onEnterBack: () => {
        gsap.to(document.querySelector('.hero .hero_scroll'), {
          duration:0.3,
          opacity: 1
        })
      },
      pinSpacing: false,
      markers: false,
    });

    const animationTrigger = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: document.querySelector("#hero"),
      scrub: 0,
      start: "top top",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: document.querySelectorAll('.hero-content-wrap'),
      pinSpacing: false,
      markers: false,
    });

    const animationTrigger3 = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: document.querySelector("#hero"),
      scrub: 0,
      start: "top top",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: document.querySelectorAll('.hero-bg'),
      pinSpacing: false,
      markers: false,
    });

    this.snap = new Snap({
      element: document.querySelector('[data-mobile-snap]'),
      goToPoint: (point, duration, index, pointElem,) => {
        
      },
      hasPin: false,
      stickOnInit: true,
      mobileOffset: -60
    });

    this.snap.init();
  }

  init() {
    console.log('check');
    document.addEventListener(
      "preloaderDone",
      (e) => {
        this.preloaderAnim = e.detail.preloaderAnim;
        this.endFrame = e.detail.endFrame;

        this.initAnimation();
      },
      { once: true }
    );
  }
}

export default function () {
  console.log('check init')
  document.querySelectorAll("[data-hero-a2]").forEach((holder, i) => {
    let hero = new Hero({holder});

    hero.init();
  });
}
