import lottie from 'lottie-web';
import gsap from 'gsap';
import $ from "jquery";
import utils from '../utils';
let { isTouchDevice, onSwipe, Wheel, isMobile } = utils;

function MenuBtn({element}){
    this.element = element;
    this.menu = document.querySelector('.menu');
    this.inited = false;

    const animationSettings = {
        renderer: 'svg',
        loop: true,
        autoplay: false,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice',
            className: '',
        }
    }
    
    this.initAnimation = () => {
        this.animation = lottie.loadAnimation({
            ...animationSettings,
            path: '../scripts/lottie/clouds-loop/data.json',
            container: document.querySelector('.menu_bg-inner'),
        })
    }

    this.setDefault = function() {
        $('body').removeClass('menu-transition');
        gsap.set(this.menu.querySelectorAll('[data-fadeout]'), {
            opacity:1,
        })
        gsap.set(document.querySelectorAll('[data-scaledown]'), {
            scale: 1,
        })
        gsap.set(document.querySelector('[data-mask]'), {
            scale: 1,
        })
        gsap.set(this.menu.querySelector('[data-menu-bg]'), {
            opacity: 1
        })
    }

    this.animateMenu = function(activeWindow) {
        let timeline = gsap.timeline();

        let duration = 20;

        console.log(`${activeWindow == 1 ? 'center' : (-(activeWindow-1)*20) + '%'} center`);

        setTimeout(() => {
            ev('newPageTransitionEnd', {});
        }, 2000)

        timeline
            .eventCallback('onComplete', () => {
                $('.btn-menu-toggle').trigger('click');
                $('.btn-menu-toggle').removeClass('-blue');

                setTimeout(() => {
                    this.setDefault();
                }, 500)
            })
            .to(this.menu.querySelectorAll('[data-fadeout]'), {
                opacity:0,
                duration: 1
            }, 0)
            .to($('[data-scaledown]').eq(activeWindow).siblings(), {
                scale: 0,
                duration: 1,
                transformOrigin: 'center'
            })
            .to(document.querySelectorAll('[data-scaledown]')[activeWindow], {
                scale: 6,
                duration: 3,
                transformOrigin: `${activeWindow == 1 ? 'center' : (-(activeWindow-1)*20) + '%'} center`
            }, '-=1')
            .to(this.menu.querySelector('[data-menu-bg]'), {
                opacity: 0,
                duration: 1
            }, 0.5)
            // .to(this.menu, {
            //     opacity: 0,
            //     duration: 4,
            //     transformOrigin: '20% center'
            // })
        
        window.menutl = timeline;

        
    }

    this.initMobile = function(){
        const segments = new URL(location.href).pathname.split('/');
        const lastPath = segments.pop() || segments.pop();

        $('.menu_text .menu_item').removeClass('is-active');

        document.querySelectorAll('.menu_text .menu_item').forEach((el, i) => {
            const href = `${location.origin}${segments.join('/')}/${el.getAttribute('href')}`;
            if(location.href.includes(el.getAttribute('href'))) el.classList.add('is-active')
            if(!this.inited) el.setAttribute('href', href);
        })

        this.inited = true;
    }

    this.init = function() {

        this.initAnimation();
        // if(isMobile) this.initMobile();

        let self = this;

        $(".btn-menu-toggle").on('click', function () {
            console.log('click menu btn');
            
            $(this).toggleClass("-active");
            $(".menu").slideToggle();

            if(!$(this).hasClass('-active')) {
                self.animation.pause();
            } else {
                self.animation.play();
            }
          });

        $('.menu_item.-hover').hover(function() {
            if($(this).hasClass('-soon')) {
                $('.menu_text .menu_item').eq($(this).index()).addClass('-soon');
            } else {
                $('.menu_text .menu_item').eq($(this).index()).addClass('-active');
            }
            $('.menu_bg-hovers div').eq($(this).index()).addClass(' -active');
            $('.menu_bg-shape').addClass(' -active');
        }, function() {
            $('.menu_text .menu_item').removeClass('-active -soon');
            $('.menu_bg-hovers div').removeClass('-active');
            $('.menu_bg-shape').removeClass(' -active');
        })

        let page = document.querySelector('[data-scroll-container]');

        $('.menu_item.-hover').click(function(e) {
            e.preventDefault();

            const href = $(this).attr('href');

            if(location.href.includes(href)) {
                $(".btn-menu-toggle").trigger('click');
                return;
            }

            lscroll.scrollTo(0, {duration: 0, disableLerp: true, callback: () => {
                lscroll.stop();
            }});

            const segments = new URL(location.href).pathname.split('/');
            const lastPath = segments.pop() || segments.pop();

            const nextHref = `${location.origin}${segments.join('/')}/${href}`;

            page.innerHTML = '';

            fetch(nextHref).then(function (response) {
                // The API call was successful!
                return response.text();
            }).then((html) => {

                $('body').addClass('menu-transition');

                // Convert the HTML string into a document object
                var parser = new DOMParser();
                var doc = parser.parseFromString(html, 'text/html');

                // console.log(doc)
                page.innerHTML = doc.querySelector('[data-scroll-container]').innerHTML;

                gsap.to(document.querySelectorAll('.btn-menu-toggle'), {
                    opacity:0,
                    duration: 0.1
                })

                ev('newPageTransitionStart', {nextHref});

            }).catch(function (err) {
                // There was an error
                console.warn('Something went wrong.', err);
            });

            // timeline.play();
            console.log($(this).index());
            self.animateMenu($(this).index());
        })
    }

    this.initBtnColorToggle = () => {
        console.log('if part 2', document.querySelector('[data-scroll-call="part-2"]'))

        document.querySelector('[data-scroll-call="part-2"]') && document.querySelector('[data-scroll-call="part-2"]').addEventListener('CallEvent', (e) => {
            let {direction, way} = e.detail;

            console.log('init [data-scroll-call="part-2"]');

            if(direction == "down" && way == "enter") {
                document.querySelector('.btn-menu-toggle').classList.add('-blue');
            }

            if(direction == "up" && way == "exit") {
                document.querySelector('.btn-menu-toggle').classList.remove('-blue');
            }
        });
    }
}

export default () => {
    if(document.querySelector('.menu')) {
        let menuBtn = new MenuBtn({element: document.querySelector('.menu')});
        menuBtn.init();

        return menuBtn;
    }
}