import gsap from 'gsap';

function ClipBg({element}){
    this.element = element;

    this.init = function() {
        this.animation = gsap.timeline({paused: true});

        this.animation
            // .fromTo(document.querySelector('.part-3-bg'), {
            // 	transform: 'translate3d(0,150vh,0)'
            // },{
            // 	transform: 'translate3d(0,0vh,0)',
            // 	ease: 'none',
            // 	duration:1
            // })

            .eventCallback("onStart", function() {console.log('clipbg.js onStart');
                document.body.dispatchEvent(new Event('2022-sia-preflight-sectionchatbot-end'));
            })
            .to(document.querySelector('.part-3-bg_inner.-new'), {
                scale: 5.7,
                ease: 'none',
                duration:1
            })
            .addLabel('bgScale')
            .to(document.querySelectorAll('.-part-3-out'), {
                opacity: 0,
                // y: -200,
                ease: 'none',
                duration:0.3
            }, '0.3')
            .set(document.querySelectorAll('.part-3-bg_inner.-light'), {
                opacity: 1,
                ease: 'none',
            }, 'bgScale-=0.1')
            

            // .fromTo(document.querySelector('.part-3-bg'), {
            // 	transform: 'translate3d(0,150vh,0)'
            // },{
            // 	transform: 'translate3d(0,0vh,0)',
            // 	ease: 'none',
            // 	duration:1
            // })
            // .to(document.querySelector('.part-3-bg_inner.-light'), {
            // 	"clip-path": "ellipse(200% 100% at 50% 100%)",
            // 	ease: 'none',
            // 	duration:0.8
            // }, "0.2")

        document.addEventListener('ScrollEvent', (e) => {
            var args = e.detail.args;
    
            if(typeof args.currentElements['clip-bg'] === 'object') {
                let progress = args.currentElements['clip-bg'].progress;

                this.animation.progress(progress)
            }
        });
    }
}

export default () => {
    let clipBg = new ClipBg({element: document.querySelector('.part-3')});

    clipBg.init();
}
