import lottie from 'lottie-web';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import $ from "jquery";
import utils from '../utils';

let { isMobile, Wheel, isPortrait, isTouchDevice, onSwipe, isTabletPortrait } = utils;

function App({element}){
    this.element = element;

    let isScrolling = false;
    let isStick = false;
    let counter = 0;
    let currentPosition = 0;

    this.init = function() {
        this.animation = gsap.timeline({paused: true});
        let playhead = {frame: 15};

        let appAnimation = lottie.loadAnimation({
            container: this.element.querySelector('.app-animation_holder'),
            renderer: 'svg',
            loop: false,
            autoplay: false,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice',
                className: 'app-animation-svg',
            },
            path: '../scripts/lottie/app/data.json'
        })

        appAnimation.addEventListener("DOMLoaded", () => {

            window.lscroll.update();

            let textOffset = 950;
             
            this.animation
              .delay(0.2)
              .to(document.querySelector('#app-text-intro'), {
                y: -textOffset,
                duration: 2,
                ease: 'power1.in',
                onStart: () => {
                  window.lscroll.update();
                }
              }, "0")
              // .to(document.querySelector('.part-3-bg_inner.-new'), {
              //   y: 2000,
              //   ease: 'none',
              //   duration:10
              // }, '0')
              .from(document.querySelector('#app-text-food'), {
                y: textOffset,
                duration: 2,
              }, "0.5")
              .set(document.querySelector('#app-text-food .text-border'), {className: "+=is-inview"}, '-=0.5')
              .set(document.querySelector('.tooltip.-food'), {opacity: 1, scale: 1, pointerEvents: "all"})
              .to(playhead, {
                frame: 90,
                duration: 10,
                ease: "none",
                onUpdate: () => appAnimation.goToAndStop(playhead.frame, true),
              }, '0')
              .addLabel('food')
              .to(document.querySelector('.part-3'), {
                color: '#ffffff',
                duration: 1,
                ease: 'power2.out'
              }, "food")
              .to(document.querySelector('.part-3-bg_inner.-dark'), {
                opacity: 1,
                duration: 1,
                ease: 'power2.out'
              }, "food")
              .set(document.querySelector('.btn-menu-toggle'), {className: "-=-blue"}, "food")
              .set(document.querySelector('.tooltip.-food'), {opacity: 0, scale: 0.5, pointerEvents: "none"})
              .set(document.querySelector('.tooltip.-magazine'), {opacity: 1, scale: 1, pointerEvents: "all"})
              .to(document.querySelector('#app-text-food'), {
                y: -textOffset,
                duration: 2,
                ease: 'power1.in'
              }, "food-=0.4")
              .from(document.querySelector('#app-text-moovie'), {
                y: textOffset,
                duration: 2,
              }, "food+=0.6")
              .set(document.querySelector('#app-text-moovie .text-border'), {className: "+=is-inview"}, '-=0.5')
              .to(playhead, {
                frame: 165,
                duration: 10,
                ease: "none",
                onUpdate: () => {
                  if (playhead.frame > 100) {
                    document.body.dispatchEvent(new Event('2022-sia-preflight-section-sqapp-mid'));
                  }
                  appAnimation.goToAndStop(playhead.frame, true)
                },
              }, "food")
              .addLabel('moovie')
              .to(document.querySelector('#app-text-moovie'), {
                y: -textOffset,
                duration: 2,
                ease: 'power1.in'
              }, "moovie")
              .from(document.querySelector('#app-text-magazine'), {
                y: textOffset,
                duration: 2,
              }, "moovie+=0.6")
              .set(document.querySelector('#app-text-magazine .text-border'), {className: "+=is-inview"}, '-=0.5')
              .set(document.querySelector('.tooltip.-magazine'), {opacity: 0, scale: 0.5, pointerEvents: "none"})
              .to(playhead, {
                frame: 246,
                duration: 10,
                ease: "none",
                onUpdate: () => appAnimation.goToAndStop(playhead.frame, true),
                onComplete: () => {
                  window.lscroll.update();
                }
              }, "moovie")
          })

          console.log(document.querySelector('.app').offsetHeight)

        const animationTrigger = ScrollTrigger.create({
          scroller: '[data-scroll-container]',
          trigger: document.querySelector('.app'),
          animation: this.animation,
          scrub: 1,
          start: "top top",
          end: "bottom bottom",
          // onUpdate: self => {
          //   console.log(self)
          // }, 
          onEnter: self => {
            isStick = true;
              lscroll.stop();
              console.log(123);
          },
          onEnterBack: self => {
            isStick = true;
              lscroll.stop();
          },
          // onUpdate: self => console.log("progress", self.progress),
          pin: document.querySelector('.app-animation'),
          pinSpacing: false,
          markers: false,
        });

        document.addEventListener('ScrollEvent', (e) => {
            var args = e.detail.args;
    
            if(typeof args.currentElements['app'] === 'object') {
                let progress = args.currentElements['app'].progress;

                // this.animation.progress(progress)
            }
        });

        this.initSnap();
    }

    this.initSnap = function() {

      let options = {
          onUp: function(){
              onScrollEnd('down')
          },
          onDown: function(){
              onScrollEnd('up')
          }
      };

      if(isTouchDevice) {
        new onSwipe(this.element, options);
      } else {
        new Wheel(this.element, options)
      }

      function onScrollEnd(direction) {
          let sectionHeight = document.querySelector('#app-point-1').offsetHeight;

          if(direction == 'down' && !isScrolling && isStick) {

              if(counter < 3) {
                  isScrolling = true;
                  counter++;
                  let nextPoint = document.querySelector(`#app-point-${counter}`);
                  let duration = 1500 * (Math.abs(nextPoint.offsetTop - currentPosition)/sectionHeight);
      
                  lscroll.scrollTo(nextPoint, {
                      // duration: 1500,
                      duration: duration,
                      offset: -100,
                      callback: () => {
                          currentPosition = nextPoint.offsetTop;
                          isScrolling = false;
                      }
                  })
              } else {
                  lscroll.start();
                  console.log('lscroll.start')
                  isStick = false;
              }
          }

          if(direction == 'up' && !isScrolling && isStick) {

            if(counter > 0) {
              isScrolling = true;
              counter--;                
              let nextPoint = document.querySelector(`#app-point-${counter}`);
              let duration = 1500 * (Math.abs(nextPoint.offsetTop - currentPosition)/sectionHeight);

              lscroll.scrollTo(nextPoint, {
                  // duration: 1500,
                  duration: duration,
                  offset: counter == 0 ? 0 :-100,
                  callback: () => {
                      currentPosition = nextPoint.offsetTop;
                      isScrolling = false;
                  }
              })
            } else {
              lscroll.start();
              console.log('lscroll.start')

              isStick = false;
            }
          }
      }
    }

    this.initMobile = function() {

      const bgTrigger = ScrollTrigger.create({
        scroller: '[data-scroll-container]',
        trigger: document.querySelector('[data-bg-change]'),
        // animation: this.animation,
        scrub: false,
        start: "top center",
        end: "bottom bottom",
        onEnter: () => {
          $('.bg-wrap').addClass('-dark');
          gsap.set(document.querySelector('.btn-menu-toggle'), {className: "-=-blue"})
          
          $('[data-color-change]').addClass('text-white');
          lscroll.update();
        },
        onLeaveBack: () => {
          $('.bg-wrap').removeClass('-dark');
          gsap.set(document.querySelector('.btn-menu-toggle'), {className: "+=-blue"})

          $('[data-color-change]').removeClass('text-white');
        },
        
        pinSpacing: false,
        markers: false,
      });

      document.querySelectorAll('[data-autoplay]').forEach((element, index) => {

        let animation = lottie.loadAnimation({
            container: element,
            renderer: 'svg',
            loop: false,
            autoplay: false,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMax slice',
            },
            path: '../scripts/lottie/mobile/app/'+ (index+1) +'/data.json'
        })
        
        element.addEventListener('CallEvent', function(e){
          var obj = e.detail.obj;

          if (obj.inView) {
            animation.play();
          }
        });
      });
    }

    this.initTitle = () => {
      this.title = document.querySelector('#title-2');
      let animation = gsap.timeline({ paused: true });

      this.title.querySelectorAll("div").forEach((elem) => {
        let speed = isMobile && elem.dataset.speedMobile ? +elem.dataset.speedMobile : +elem.dataset.scrollSpeed;

        animation.from(
          elem,
          {
            xPercent: speed * 10,
            ease: "none",
            duration: 1,
          },
          "0"
        );
      });

      let offset = isMobile && this.title.dataset.mobileOffset ? this.title.dataset.mobileOffset : 'top';

      const animationTrigger = ScrollTrigger.create({
        scroller: "[data-scroll-container]",
        trigger: this.title,
        animation: animation,
        scrub: 0.5,
        start: (isTabletPortrait ? '200%' : offset) + " bottom",
        end: "center center",
        // onUpdate: self => console.log("progress", self.progress),
        onEnter: () => {
          lscroll.update();
        },
        pin: false,
        pinSpacing: false,
        markers: false,
      });

      lscroll.update();
    }
}

export default function() {
  document.querySelectorAll('#app').forEach((elem) => {
    let app = new App({element: elem});

    isPortrait ? app.initMobile() : app.init();
    app.initTitle();
  })
}