import $ from "jquery";
import LocomotiveScroll from "locomotive-scroll";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";
import LazyLoad from "vanilla-lazyload";
import className from "./className";

import initTitle from "./components/title";
import snap from "./components/snap";
import initHero from "./components/hero";
import initHeroA2 from "./components/heroA2";
import initHeroA3 from "./components/a3/hero";
import initChatbot from "./components/chatbot";
import initQuote from "./components/quote";
import initApp from "./components/app";
import initClipBg from "./components/clipBg";
import initMenu from "./components/menu";
import initBgZoomOut from "./components/bgZoomOut";
import initPreloader from "./components/preloader";
import initCheckin from "./components/a2/checkin";
import initLounge from "./components/a2/lounge";
import initBoarding from "./components/a2/boarding";
import initCerts from "./components/a2/certs";
import initGallery from "./components/a2/gallery";
import initCabin from "./components/a3/cabin";
import initTravel from "./components/a3/travel";
import initCnnBar from "./components/cnnBar";
import utils from './utils';
let { isMobile, onSwipe, Wheel, isTouchDevice, isTabletSm, isPortrait } = utils;

var ev = function (eventName, data, target) {
  var target = target ? target : document;
  var e = new CustomEvent(eventName, { detail: data });
  target.dispatchEvent(e);
};

window.ev = ev;

$(document).ready(function () {

  function isInView($el, scrollTop) {
    var elTop = $el.offset().top;
    return (
      scrollTop <= elTop + $el.outerHeight() && scrollTop + winHeight >= elTop
    );
  }

  gsap.registerPlugin(SplitText);
  gsap.registerPlugin(ScrollTrigger);
  gsap.registerPlugin(className);

  $('[data-mob-noscorll]').removeAttr('data-scroll');

  //Scroll
  var lscroll = new LocomotiveScroll({
    el: document.querySelector("[data-scroll-container]"),
    scrollFromAnywhere: true,
    smooth: true,
    smartphone: {
      smooth: true,
      direction: "vertical",
    },
    tablet: {
      smooth: true,
      direction: "vertical",
    },
    lerp: 0.1,
    multiplier: isMobile ? 1 : 0.4,
    getDirection: true,
  });

  // tell ScrollTrigger to use these proxy methods for the ".smooth-scroll" element since Locomotive Scroll is hijacking things
  ScrollTrigger.scrollerProxy("[data-scroll-container]", {
    scrollTop(value) {
      return arguments.length
        ? lscroll.scrollTo(value, 0, 0)
        : lscroll.scroll.instance.scroll.y;
    }, // we don't have to define a scrollLeft because we're only scrolling vertically.
    getBoundingClientRect() {
      return {
        top: 0,
        left: 0,
        width: window.innerWidth,
        height: window.innerHeight,
      };
    },
    // LocomotiveScroll handles things completely differently on mobile devices - it doesn't even transform the container at all! So to get the correct behavior and avoid jitters, we should pin things with position: fixed on mobile. We sense it by checking to see if there's a transform applied to the container (the LocomotiveScroll-controlled element).
    pinType: document.querySelector("[data-scroll-container]").style.transform
      ? "transform"
      : "fixed",
  });

  window.lscroll = lscroll;

  // ScrollTrigger.addEventListener('refresh', () => lscroll.update());

  window.addEventListener(
    "resize",
    function (event) {
      console.log(123);
      ScrollTrigger.refresh();
    },
    true
  );

  // lscroll.stop();

  lscroll.on("scroll", function (args) {
    // console.log(123);
    ScrollTrigger.update();

    if (typeof args.currentElements["lockScroll"] === "object") {
      let progress = args.currentElements["lockScroll"].progress;
    }

    ev("ScrollEvent", { args });

    var instance = lscroll.scroll.instance;

    // console.log(instance);
    var p = instance.scroll.y / instance.limit.y;
    if (p > 0.25) {
      document.body.dispatchEvent(new Event('2022-sia-preflight-progress-25'));
    }
    if (p > 0.50) {
      document.body.dispatchEvent(new Event('2022-sia-preflight-progress-50'));
    }
    if (p > 0.75) {
      document.body.dispatchEvent(new Event('2022-sia-preflight-progress-75'));
    }
    if (p > 0.95) {
      document.body.dispatchEvent(new Event('2022-sia-preflight-progress-100'));
    }
    $('.progress_bar').css('transform', `scaleX(${p})`);
  });

  lscroll.on("call", function (value, way, obj) {
    if (value == "title") {
    }

    ev(
      "CallEvent",
      {
        value,
        way,
        obj,
        direction: lscroll.scroll.instance.direction,
      },
      obj.el
    );
  });

  setTimeout(function () {
    lscroll.update();
  }, 1000);


  $('body').on('click', '.tooltip_toggle', function(){

      $(this).parent().siblings().find('.tooltip_toggle, .tooltip_content').removeClass('-active');
      $(this).parent().siblings().removeClass('is-active');
      $(this).parent().toggleClass('is-active');

      $(this).toggleClass('-active');
      $(this).parent().find('.tooltip_content').toggleClass('-active');
  })

  /* Animations */

  initPreloader();
  initHero();
  initHeroA2();
  initHeroA3();

  initCnnBar();

  // initPart3();

  gsap.delayedCall(0.3, () => {
    lscroll.update();
  });

  function Next(){

    let options = {
      onUp: function(){
          onScrollEnd('down')
      },
      onDown: function(){
          onScrollEnd('up')
      }
    };

    this.init = () => {
      this.wrap = document.querySelector('.next-article');
      this.isTriggered = false;
      this.nextArticle = '';

      if(isTouchDevice) {
          var handleSwipe = new onSwipe(this.wrap, options);
      } else {
          let handleWheel = new Wheel(this.wrap, options);
      }
    }

    const onScrollEnd = (direction) => {
      const instance = lscroll.scroll.instance;

      let diff = Math.abs(instance.scroll.y - instance.limit.y);

      if(direction == 'down' && !this.isTriggered && diff <= 2) {
        this.isTriggered = true;
        window.inTransition = true;
        $('.next-article.-a2 .hero-bg').css({opacity:1})
        $('[data-scroll-container]').addClass('in-transition');
        this.appendPage();

        if($('body').hasClass('a2')) {
          $('body').removeClass('a2');
        } else {
          $('body').addClass('a2');
        }

        gsap.to(document.querySelector('.next-article_note'), {
          opacity: 0,
          duration: 0.3
        })

        this.titleOut(() => {
          $(this.wrap.querySelector('.next-article_title')).hide();

          lscroll.scrollTo(0, {duration: 0, disableLerp: true, callback: () => {
            lscroll.stop();
          }});

          setTimeout(() => {
            this.oldHero.remove();
            this.oldPage.remove();
            lscroll.update();
          }, 0)

          this.nextTL.play();
        });
      }

      if(direction == 'up') {

      }
    }

    this.transit = () => {
    }

    this.initTitle = () => {

      const nextArticleBg = document.querySelector('.next-article-bg');

      const bgAnimation = ScrollTrigger.create({
        scroller: "[data-scroll-container]",
        trigger: document.querySelector('.next-article-bg-trigger'),
        start: "top top",
        end: `bottom bottom`,
        // onUpdate: self => console.log("progress", self.progress),
        pin: nextArticleBg,
        pinSpacing: false,
        markers: false,
      });

      const colorChange = ScrollTrigger.create({
        scroller: "[data-scroll-container]",
        trigger: document.querySelector('.next-article'),
        start: "top center",
        end: `bottom bottom`,
        // onUpdate: self => console.log("progress", self.progress),
        onEnter: (self) => {
          console.log('enter')
            gsap.to(nextArticleBg, {
            duration: 1,
            opacity: 1
          })
        },
        onLeaveBack: (self) => {
          console.log('leave')

          gsap.to(nextArticleBg, {
            duration: 1,
            opacity: 0
          })
        },
        pin: false,
        pinSpacing: false,
        markers: false,
      });

      let animation = gsap.timeline({paused: true});

      this.line1 = this.wrap.querySelector('.next-article_title div:nth-child(1)');
      this.line2 = this.wrap.querySelector('.next-article_title div:nth-child(2)');
      this.line3 = this.wrap.querySelector('.next-article_title div:nth-child(3)');

      animation
        .fromTo(this.line1, {
          xPercent: 60
        }, {
          duration: 1,
          xPercent: 0,//xPercent: -70,
          ease: 'none'
        }, '0')
        .fromTo(this.line2, {
          xPercent: -60
        }, {
          duration: 1,
          xPercent: 0,//xPercent: 75,
          ease: 'none'
        }, '0')
        .fromTo(this.line3, {
          xPercent: 70
        }, {
          duration: 1,
          xPercent: 0,//xPercent: -75,
          ease: 'none'
        }, '0')

        const animationTrigger = ScrollTrigger.create({
          scroller: "[data-scroll-container]",
          trigger: this.wrap,
          animation: animation,
          scrub: 0.5,
          start: "30% bottom",
          end: "bottom bottom",
          // onUpdate: self => console.log("progress", self.progress),
          pin: false,
          pinSpacing: false,
          markers: false,
        });
    }

    this.titleOut = (callback) => {
      gsap.to(this.line1, {
        duration: 1,
        xPercent: -70,
        opacity:0,
        ease: 'none'
      })
      gsap.to(this.line2, {
        duration: 1,
        xPercent: 75,
        opacity:0,
        ease: 'none'
      })
      gsap.to(this.line3, {
        duration: 1,
        xPercent: -75,
        opacity:0,
        ease: 'none',
        onComplete: callback
      })
    }

    this.load = () => {
      this.init();

      const href = document.querySelector('#next-article').getAttribute('data-href');

      const segments = new URL(location.href).pathname.split('/');
      const lastPath = segments.pop() || segments.pop();

      this.nextHref = `${location.origin}${segments.join('/')}/${href}`;

      fetch(this.nextHref).then(function (response) {
          return response.text();
      }).then((html) => {
          var parser = new DOMParser();
          this.nextArticle = parser.parseFromString(html, 'text/html');
          this.oldHero = document.querySelector('.hero');
          this.oldPage = document.querySelector('.page');

          $(this.wrap).append(this.nextArticle.querySelector('.hero').outerHTML);

          this.preloader = initPreloader(document.querySelector('.next-article'), false);

          this.initTitle();

          this.nextTL = gsap.timeline({paused: true})

          this.nextTL
            .to(document.querySelector('.next-article .preloader'), {
              yPercent: 0,
              y:0,
              duration: 1,
              onComplete: () => {
                $('[data-scroll-container]').removeClass('in-transition');
                $('.btn-menu-toggle').removeClass('-blue');
                this.preloader.start();
                window.history.replaceState('', '', new URL(this.nextHref).pathname );

                ev('newPage', {});
        
                this.wrap.classList.add('is-loaded');
              }
            }, '0')

          // ev('newPage', {});

      }).catch(function (err) {
          console.warn('Something went wrong.', err);
      });
    }

    this.appendPage = () => {
      $(this.wrap).append(this.nextArticle.querySelector('.page').outerHTML);
      $(this.wrap).find('.footer').show();
    }
  }

  let next = new Next();

  let menu = initMenu();

  document.addEventListener("preloaderStarted", () => {
    console.log('preloaderStarted')
    if($('.next-article').length && !$('.next-article').hasClass('is-loaded')) next.load();
  }, { once: false });

  document.addEventListener(
    "preloaderDone",
    () => {

      console.log('preloaderDone')
      
      $('.btn-menu-toggle').removeClass('-blue');
      menu.initBtnColorToggle();
      if(isMobile) menu.initMobile();

      initTitle();

      initChatbot();
      initQuote();
      initClipBg();
      initApp();
      initBgZoomOut();
      // initPart3();

      initCerts();
      initCheckin();
      initLounge();
      initGallery();
      initBoarding();

      //

      initCabin();
      initTravel();

      gsap.delayedCall(0.3, () => {
        lscroll.update();

        const lazyInstance = new LazyLoad({
          container: document.body,
          elements_selector: '[data-component="lazyload"]',
          threshold: window.innerHeight * 4,
          // thresholds: '9000px 0px 0px 0px',
          unobserve_entered: true,
        });
      });
    },
    { once: false }
  );
    
  snap();

  document.addEventListener(
    "newPage",
    () => {

      console.log('newPage')

      lscroll.update();
    
      snap();
      initHero();
      initHeroA2();

    },
    { once: false }
  );

  let nextPreloader;

  document.addEventListener(
    "newPageTransitionStart",
    (e) => {

      console.log('newPageTransitionStart',  e.detail.nextHref);
      window.inTransition = true;
      lscroll.update();

      window.history.replaceState('', '', new URL(e.detail.nextHref).pathname);
    
      snap();

      nextPreloader = initPreloader(document, false);
      initHero();
      initHeroA2();
    },
    { once: false }
  );

  document.addEventListener(
    "newPageTransitionEnd",
    () => {

      console.log('newPageTransitionEnd')

      nextPreloader.start();

    },
    { once: false }
  );

  //scroll hero
  // var lscroll2 = new LocomotiveScroll({
  //   el: document.querySelector(".hero-scroll"),
  //   scrollFromAnywhere: true,
  //   smooth: true,
  //   smartphone: {
  //     smooth: true,
  //     direction: "vertical",
  //   },
  //   tablet: {
  //     smooth: true,
  //     direction: "vertical",
  //   },
  //   lerp: 0.1,
  //   multiplier: 0.4,
  //   getDirection: true,
  // });

});
