import { ScrollTrigger } from "gsap/ScrollTrigger";
import $ from "jquery";
import gsap from "gsap";

function initCerts(certs) {
    // const certs = document.querySelector("#certs-svg");
    const certsOut = certs.querySelectorAll(".cert");

    certs.classList.add('is-loaded');
	
    fetch("../images/a2/certs.svg")
      .then((r) => r.text())
      .then((text) => {
        // certs.innerHTML = text;
        lscroll.update();
		    setAnimation();
      })
      .catch(console.error.bind(console));

    function setAnimation() {
      const animation = gsap.timeline({ paused: true });

      animation.fromTo(certs.querySelectorAll(".cert div"), {
        y: (i, target, targets) => {
          return (i+1) * -15
        },
      },{
        y: (i) => {
          return (i+1) * 5
        },
      //   stagger: 0.1,
        duration: 0.3
      });
    
      const animationTrigger = ScrollTrigger.create({
        scroller: "[data-scroll-container]",
        trigger: certs,
        animation: animation,
        scrub: 2,
        start: "top bottom",
        end: "bottom bottom",
        // onUpdate: self => console.log("progress", self.progress),
        pinSpacing: false,
        markers: false,
      });

      var timeout;
      $(certs).mousemove(function(e){
        callParallax(e);
      });

      function callParallax(e){
        certsOut.forEach((element, i) => {
          parallaxIt(e, element, i*-10)
        });
      }

      function parallaxIt(e, target, movement){
        var $this = $(certs);
        var relX = e.pageX - $this.offset().left;
        var relY = e.pageY - $this.offset().top;
        
        gsap.to(target, {
          x: (relX - $this.width()/2) / $this.width() * movement,
          y: (relY - $this.outerHeight()/2) / $this.outerHeight() * movement,
          duration:1,
          ease: 'power2.easeOut'
        })
      }

      // gsap.set(".ball", {xPercent: -50, yPercent: -50});

      // var ball = certs;
      // var pos = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
      // var mouse = { x: pos.x, y: pos.y };
      // var speed = 0.1;

      // var fpms = 60 / 1000;

      // var xSet = gsap.quickSetter(ball, "x", "px");
      // var ySet = gsap.quickSetter(ball, "y", "px");

      // document.querySelector('.part-2').addEventListener("mousemove", e => {    
      //   mouse.x = e.x;
      //   mouse.y = e.y;  
      // });

      // gsap.ticker.add((time, deltaTime) => {
        
      //   var delta = deltaTime * fpms;
      //   var dt = 1.0 - Math.pow(1.0 - speed, delta); 

      //   console.log(dt);
      //   console.log(mouse.x)
      //   console.log(mouse.y)
        
      //   // pos.x += (mouse.x - pos.x) * dt;
      //   // pos.y += (mouse.y - pos.y) * dt;
      //   xSet(0);
      //   ySet(0);
      // });
    }
  }

  export default () => {
    document.querySelectorAll("#certs-svg").forEach(element => {
        initCerts(element);
    });
  }