import gsap from 'gsap';
import lottie from 'lottie-web';
import utils from '../utils';

let { isMobile } = utils;

class Chatbot {
    constructor({holder}) {
        this.holder = holder;
    }

    init() {
        let lottieTL = gsap.timeline({paused: true});
        let playhead = {frame: 0};
        
        let animation = lottie.loadAnimation({
          container: document.querySelector('.chatbot'),
          renderer: 'svg',
          loop: false,
          autoplay: false,
          rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice',
            className: 'chatbot_animation',
          },
          path: isMobile ? '../scripts/lottie/mobile/chatbot/data.json' : '../scripts/lottie/chatbot/data.json'
        })
        
        animation.addEventListener("DOMLoaded", () => {
				  window.lscroll.update();

          lottieTL
            .eventCallback("onUpdate", function() {
              if (this.time() > 15) {
                document.body.dispatchEvent(new Event('2022-sia-preflight-sectionchatbot-mid'));
              }
              //
            })
            .eventCallback("onComplete", function() {
              document.body.dispatchEvent(new Event('2022-sia-preflight-sectionchatbot-mid'));
            })
            .to(playhead, {
              frame: animation.totalFrames - 1,
              duration: animation.getDuration(false),
              ease: "none",
              onUpdate: () => animation.goToAndStop(playhead.frame, true),
            })
            // .to(document.querySelector('.chatbot-pre-text'), {
            //   opacity: 0,
            //   y: -60,
            //   duration: 0.3
            // }, "0")
            // .from(document.querySelector('.chatbot_animation'), {
            //   opacity: 0,
            //   scale: 0.7,
            //   duration: 0.3
            // }, '-=0.1')
            // .to(playhead, {
            //   frame: animation.totalFrames - 1,
            //   duration: 10,
            //   ease: "none",
            //   onUpdate: () => animation.goToAndStop(playhead.frame, true),
            // })
        })

        this.holder.addEventListener('CallEvent', function(e){
          var obj = e.detail.obj;

          if (obj.inView) {
            lottieTL.play();
          }
        });

        document.addEventListener('ScrollEvent', function(e){
          var args = e.detail.args;
  
          if(typeof args.currentElements['chatbot'] === 'object') {
                let progress = args.currentElements['chatbot'].progress;

                // lottieTL.progress(progress)
            }
        });
    }
}

export default function () {
  document.querySelectorAll('[data-scroll-id="chatbot"]').forEach((holder, i) => {
    let chatbot = new Chatbot({holder});

    chatbot.init();
  })
}

