import lottie from "lottie-web";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Snap from "../useSnap";
import utils from '../../utils';
import locomotiveScroll from "locomotive-scroll";
let { isTouchDevice, onSwipe, Wheel, isMobile } = utils;

gsap.registerPlugin(ScrollTrigger);

class Travel {
  constructor({holder}) {
    this.holder = holder;
    this.animationWrap = this.holder.querySelector('.travel-animation_wrap');
    this.tooltip = this.animationWrap.querySelector('.tooltip');
  }

  initAnimation() {

    const animationSettings = {
      renderer: 'svg',
      loop: false,
      autoplay: false,
      rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
        className: '',
      }
    }

    this.animation = lottie.loadAnimation({
      ...animationSettings,
      path: '../scripts/lottie/a3/ife/data.json',
      container: this.animationWrap,
    })

    // this.animation = gsap.timeline({ paused: true });

    // console.log(animation);

    this.animation.addEventListener("loaded_images", () => {
      // let totalFrames = animation.totalFrames - 1;

      // let playhead = { frame: this.endFrame };
  
      // this.animation
      //   .to(
      //     playhead,
      //     {
      //       frame: totalFrames,
      //       duration: 10, //2, //preloaderAnim.getDuration(false),
      //       ease: "none",
      //       onUpdate: () => animation.goToAndStop(playhead.frame, true),
      //     },
      //     "0"
      //   );
  
      // const animationTrigger = ScrollTrigger.create({
      //   scroller: "[data-scroll-container]",
      //   trigger: document.querySelector("#cabin"),
      //   animation: this.animation,
      //   scrub: 1,
      //   start: "top top",
      //   end: "bottom bottom",
      //   // onUpdate: self => console.log("progress", self.progress),
      //   pin: false,
      //   pinSpacing: false,
      //   markers: false,
      // });
    })

    this.initSnap();
  }

  initSnap(){
    let playhead = { frame: 0 };

    this.snap = new Snap({
      element: this.holder,
      goToPoint: (point, duration, index, pointElem) => {

        if(index != 3) {
          this.tooltip.classList.remove('-active');
        }

        setTimeout(() => {
          if(index == 3) {
            this.tooltip.classList.add('-active');
          }
        }, duration * 1000)

        gsap.to(playhead, {
            frame: point,
            duration: duration, //2, //preloaderAnim.getDuration(false),
            ease: "none",
            onUpdate: () => this.animation.goToAndStop(playhead.frame, true),
        });
      },
      hasPin: false,
      stickOnInit: false
    });

    this.snap.init();
  }

  init() {
    this.initAnimation();
  }
}

export default function () {
document.querySelectorAll("#travel").forEach((holder, i) => {
    let travel = new Travel({holder});

    travel.init();
  });
}
