// import { SplitText } from 'gsap/SplitText';
import lottie from "lottie-web";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Snap from "../useSnap";
import utils from '../../utils';
import locomotiveScroll from "locomotive-scroll";
let { isTouchDevice, onSwipe, Wheel, isMobile } = utils;

gsap.registerPlugin(ScrollTrigger);

class Cabin {
  constructor({holder}) {
    this.holder = holder;
    this.animationWrap = this.holder.querySelector('.cabin-animation_wrap');
    this.tooltip = this.animationWrap.querySelector('.tooltip');
  }

  initAnimation() {

    const animationSettings = {
      renderer: 'svg',
      loop: false,
      autoplay: false,
      rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
        className: '',
      }
    }

    this.animation = lottie.loadAnimation({
      ...animationSettings,
      path: '../scripts/lottie/a3/cabin/data.json',
      container: this.animationWrap,
    })

    const animation = gsap.timeline({ paused: true });

    // console.log(animation);

    this.animation.addEventListener("loaded_images", () => {
      let totalFrames = this.animation.totalFrames - 1;

      let playhead = { frame: this.endFrame };
  
      animation
        .to(
          playhead,
          {
            frame: 54,
            duration: 10, //2, //preloaderAnim.getDuration(false),
            ease: "none",
            onUpdate: () => this.animation.goToAndStop(playhead.frame, true),
          },
          "0"
        );
  
      const animationTrigger = ScrollTrigger.create({
        scroller: "[data-scroll-container]",
        trigger: document.querySelector("#cabin"),
        animation: animation,
        scrub: 0,
        start: "top 60%",
        end: "top top",
        // onUpdate: self => console.log("progress", self.progress),
        pin: false,
        pinSpacing: false,
        markers: false,
      });
    })

    this.initSnap();
  }

  initSnap(){
    let playhead = { frame: 0 };

    this.snap = new Snap({
      element: this.holder,
      goToPoint: (point, duration, index, pointElem) => {
        if(index > 0) {
          this.animationWrap.classList.add('is-scaled')
        } else {
          this.animationWrap.classList.remove('is-scaled')
        }

        if(index > 2) {
          this.tooltip.classList.add('-active');
        } else {
          this.tooltip.classList.remove('-active');
        }

        gsap.to(playhead, {
            frame: point,
            duration: 1, //2, //preloaderAnim.getDuration(false),
            ease: "none",
            onUpdate: () => this.animation.goToAndStop(playhead.frame, true),
        });
      },
      hasPin: false,
      stickOnInit: false
    });

    this.snap.init();
  }

  init() {
    this.initAnimation();
  }
}

export default function () {
document.querySelectorAll("#cabin").forEach((holder, i) => {
    let cabin = new Cabin({holder});

    cabin.init();
  });
}
