import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger);

class BgZoomOut {
    constructor({holder}) {
        this.holder = holder;
        this.target = document.querySelector(this.holder.dataset.target);
    }

    init() {
        let animation = gsap.timeline({paused: true});

        animation
            .to(this.target, {
                scale:0.97,
                duration: 1
            })
            .to(document.querySelector('.timeline'), {
                clipPath: 'inset(0 0% 0 1.5%)',
                duration: 1
            }, '0')
            .to(this.target, {
                borderRadius: "0 0 60px 60px",
                duration: 0.1
            }, '0')
    
        const animationTrigger = ScrollTrigger.create({
          scroller: '[data-scroll-container]',
          trigger: this.holder,
          animation: animation,
          scrub: true,
          start: "bottom bottom",
          end: "bottom 80%",
          // onUpdate: self => console.log("progress", self.progress),
          pin: false,
          pinSpacing: false,
          markers: false,
        });
    
        document.addEventListener('ScrollEvent', (e) => {
          var args = e.detail.args;
      
          if(typeof args.currentElements['hero-out'] === 'object') {
            let progress = args.currentElements['hero-out'].progress;
    
            // heroOutTL.progress(progress)
          }
        });
    }
  }

  export default () => {
      document.querySelectorAll('[data-bg-zoom-out]').forEach((holder, i) => {
        let bg = new BgZoomOut({holder});

        bg.init();
      })
  }