import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Video from "../useVideo";
import Snap from "../useSnap";
import utils from '../../utils';
let { isTouchDevice, onSwipe, Wheel, isMobile, isPortrait } = utils;

function Checkin({ element }) {
  this.element = element;

  this.initClouds = () => {
    this.clouds = document.querySelector(".title-clouds");
    this.cloudsAnimation = gsap.timeline({ paused: true });

    this.clouds.querySelectorAll("div").forEach((elem) => {
      let speed = +elem.dataset.scrollSpeed;

      this.cloudsAnimation.fromTo(
        elem,
        {
          xPercent: speed * 5,
        },
        {
          xPercent: speed * -5,
          ease: "none",
          duration: 1,
        },
        "0"
      );
    });

    const animationTrigger = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: this.clouds,
      animation: this.cloudsAnimation,
      scrub: 0.5,
      start: "top bottom",
      end: "bottom top",
      // onUpdate: self => console.log("progress", self.progress),
      pin: false,
      pinSpacing: false,
      markers: false,
    });
  };

  this.init = () => {
    let canvasWrap = this.element.querySelector(".checkin-animation")
    
    this.video = new Video({
      path: isPortrait ? "https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a2/sequence-mobile/AF2_Check-In_animation_Mobile_" : "https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a2/sequence/AF2_HeroAnim_ThirdPass_",
      canvas: this.element.querySelector("canvas"),
    });

    console.log(this.video);

    this.snap = new Snap({
      element: this.element,
      transitionTo: '.lounge',
      forceEnd: true,
      mobileOffset: -60,
      goToPoint: (animationPoint, duration, counter, nextPoint) => {
        this.video.goToPoint(animationPoint, duration, counter);

        if(counter > 0 && counter < 4) {
          document.querySelector('.btn-menu-toggle').classList.remove('-blue');
        } else {
          document.querySelector('.btn-menu-toggle').classList.add('-blue');
        }

        setTimeout(() => {
          canvasWrap.classList.remove('-opacity', '-white');
        }, 200)

        if(counter != 0) {
          setTimeout(() => {
            canvasWrap.classList.add('-opacity');
            if(nextPoint.hasAttribute('data-white-bg')) canvasWrap.classList.add('-white');
          }, duration*1000 - 400)
        }
      },
    });

    this.snap.init();

    this.initClouds();
  };
}

export default function () {
  document.querySelectorAll("[data-checkin]").forEach((elem) => {
    let checkin = new Checkin({ element: elem });

    checkin.init();
  });
}
