
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import utils from '../utils';
let { isMobile } = utils;
  
  class Title {
    constructor({ elem }) {
      this.title = elem;
    }

    init() {
      this.animation = gsap.timeline({ paused: true });

      this.title.querySelectorAll(":scope > div").forEach((elem) => {
        let speed = isMobile && elem.dataset.speedMobile ? +elem.dataset.speedMobile : +elem.dataset.scrollSpeed;

        this.animation.from(
          elem,
          {
            xPercent: speed * 10,
            ease: "none",
            duration: 1,
          },
          "0"
        );
      });

      let offset = isMobile && this.title.dataset.mobileOffset ? this.title.dataset.mobileOffset : 'top';

      const animationTrigger = ScrollTrigger.create({
        scroller: "[data-scroll-container]",
        trigger: this.title,
        animation: this.animation,
        scrub: 0.5,
        start: offset + " bottom",
        end: "center center",
        // onUpdate: self => console.log("progress", self.progress),
        pin: false,
        pinSpacing: false,
        markers: false,
      });

      lscroll.update();
    }
  }

  export default () => {
    document.querySelectorAll(".section-title:not(#title-2):not(.next-article_title)").forEach((elem) => {
        let title = new Title({ elem });
    
        title.init();
      });
  }