import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Video from "../useVideo";
import Snap from "../useSnap";
import utils from '../../utils';
let { isTouchDevice, onSwipe, Wheel, isMobile } = utils;

function Hero({ element }) {
  this.element = element;

  this.init = () => {
    let canvasWrap = this.element.querySelector(".checkin-animation")
    
    this.video = new Video({
      path: isMobile ? "https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a3/sequence/AF3_HeroAnim_" : "https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a3/sequence/AF3_HeroAnim_",
      canvas: this.element.querySelector("canvas"),
      counter: 927
    });

    // this.video.goToPoint(1, 1, 0);

    this.snap = new Snap({
      element: this.element,
      mobileOffset: -60,
      stickOnInit: true,
      goToPoint: (animationPoint, duration, counter, nextPoint, direction) => {
        if(counter > 0 && counter < 7) {
          document.querySelector('.btn-menu-toggle').classList.add('-blue');
        } else {
          document.querySelector('.btn-menu-toggle').classList.remove('-blue');
        }

        const ease = direction == 'down' && counter == 1 ? 'power1.out' : direction == 'up' && counter == 0 ? 'power1.in' : '';
        this.video.goToPoint(animationPoint, duration, counter, ease);
      },
    });

    this.snap.init();

    this.heroTL = gsap.timeline({paused:true});

    this.heroTL
      .to(document.querySelector('.hero_text-wrap'), {
        y: -700,
        duration: 5,
        ease: 'linear'
      })
      .to(document.querySelectorAll('.hero_text-wrap, .hero_nav'), {
        opacity:0,
        duration: 3,
        ease: 'linear'
      }, '0')
      // .to(document.querySelector('.hero_logo'), {
      //   opacity:0,
      //   duration: 3,
      //   ease: 'linear'
      // }, '0')
      .to(document.querySelector('.hero-canvas-holder'), {
        opacity:1,
        duration: 1,
        ease: 'linear'
      },'-=5')
      .set(document.querySelectorAll('.hero_content, .preloader:nth-of-type(2)'), {display: "none"})
      .to({}, {
        duration:300
      })

    const animationTrigger = ScrollTrigger.create({
      scroller: '[data-scroll-container]',
      trigger: document.querySelector('#video'),
      animation: this.heroTL,
      scrub: 0.5,
      start: "top top",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: false,
      pinSpacing: false,
      markers: false,
    });

    this.transitionAnimation = gsap.timeline({paused:true});

    this.transitionAnimation
      .to(document.querySelector('.hero-video'), {
        opacity: 0,
        duration: 20,
        ease: 'linear'
      }, '0')

    const transition = ScrollTrigger.create({
      scroller: '[data-scroll-container]',
      trigger: document.querySelector('.hero-scroll'),
      animation: this.transitionAnimation,
      scrub: 0,
      start: "bottom-=1000 top",
      end: "bottom bottom",
      onUpdate: self => console.log("progress", self.progress),
      pin: false,
      pinSpacing: false,
      markers: false,
    });

  };
}

export default function () {
  document.querySelectorAll("[data-a3-hero]").forEach((elem) => {
    let hero = new Hero({ element: elem });

    document.addEventListener("preloaderStarted", () => {
      hero.init();
    }, { once: true });

  });
}
