import $ from 'jquery';
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import lottie from 'lottie-web';
import Video from "../useVideo";
import Snap from "../useSnap";
import utils from '../../utils';
let { isTouchDevice, onSwipe, Wheel, isMobile, isPortrait } = utils;

function Lounge({ element }) {
  this.element = element;
  this.snapTrigger = this.element.querySelector('[data-snap-trigger]');
  this.playhead = { frame: 0 };
  this.canvas = this.element.querySelector("canvas");
  this.videoCounter = 210;
  let imagesPreloaded = false;
  
  const animationSettings = {
    renderer: 'svg',
    loop: false,
    autoplay: false,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
      className: '',
    }
  }

  this.initAnimation = () => {
    this.video = new Video({
      path: isPortrait ? "https://d1ipt3xviiyi7q.cloudfront.net/sia/scripts/lottie/mobile/a2/lounge/part1/Lounge-Foyer-mobile-v2-p1_" : "https://d1ipt3xviiyi7q.cloudfront.net/sia/scripts/lottie/a2/lounge/part1/Lounge-Foyer-desktop-p1",
      canvas: this.canvas,
      counter: this.videoCounter
    });

    this.videoPoints = [0, 153, 209];

    this.animation = lottie.loadAnimation({
      ...animationSettings,
      path: isPortrait ? 'https://d1ipt3xviiyi7q.cloudfront.net/sia/scripts/lottie/mobile/a2/lounge/part2/data.json' : 'https://d1ipt3xviiyi7q.cloudfront.net/sia/scripts/lottie/a2/lounge/part2/data.json',
      container: this.element.querySelector('[data-snap-animation]'),
      rendererSettings: {
        id: 'lounge-svg',
        preserveAspectRatio: 'xMidYMid slice',
      }
    })

    this.scrollArrow = this.element.querySelector('[data-lounge-scroll]');

    this.scrollArrowAnimation = lottie.loadAnimation({
      container: this.scrollArrow,
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: '../scripts/lottie/scroll-down.json'
    })
  }

  this.initPod = () => {
    this.podTimeline = gsap.timeline({paused: true});
    const pod = this.element.querySelector('[data-lounge-pod]');
    const podTop = this.element.querySelector('[data-lounge-pod-top] div');
    const podText = this.element.querySelector('[data-lounge-pod-text]');
    const podTrigger = this.element.querySelector('[data-pod-trigger]');
    let tooltips = this.element.querySelector('.lounge_tooltips-inner');
    let text = this.element.querySelector('.lounge-text');
    const podProgress = {playhead:0};
    const podTopProgress = {playhead:0};

    this.podAnimation = lottie.loadAnimation({
      ...animationSettings,
      path: '../scripts/lottie/a2/lounge/food/data.json',
      container: pod,
    })

    this.podTopAnimation = lottie.loadAnimation({
      ...animationSettings,
      path: '../scripts/lottie/a2/lounge/podTop/data.json',
      container: podTop,
    })

    let playheadOut = {frame:740};

    const animationOut = gsap.to(playheadOut, {
      frame: 780,
      duration: 0.2,
      immediateRender: false,
      ease: 'none',
      onUpdate: () => {
        console.log('triggered')
        this.animation.goToAndStop(playheadOut.frame, true);
      },
    });

    const uiOut = gsap.fromTo([tooltips, text], {
      opacity:1
    },{
      opacity:0,
      duration:0.2,
      immediateRender: false,
    });

    const foodIn = gsap.from(pod, {
      opacity:0,
      duration:0.4
    });

    const foodProgress = gsap.to(podProgress, {
      playhead: 110,
      duration: 2,
      ease: 'none',
      onUpdate: () => this.podAnimation.goToAndStop(podProgress.playhead, true),
    });

    const foodOut = gsap.to(pod, {
      opacity: 0,
      duration:0.5
    });

    let offset = 1322.9;
    let width = 601;
    let windowWidth = window.innerWidth;

    // console.log(-((windowWidth/2 - (windowWidth-offset)) + width/2))

    const postionEndIn = gsap.set(podTop, {
      x: -((windowWidth/2 - (windowWidth-offset)) + width/2),
      yPercent: -8.3,
    });

    const foodEndIn = gsap.from(podTop, {
      opacity:0,
      duration:1
    });

    const foodEndProgress = gsap.to(podTopProgress, {
      playhead: 77,
      duration: 1,
      ease: 'none',
      onUpdate: () => this.podTopAnimation.goToAndStop(podTopProgress.playhead, true),
    });

    const podTopTextIn = gsap.from(podText, {
      opacity:0,
      yPercent: 20,
      duration:0.5
    });

    const foodEndOut = gsap.to(podTop, {
      xPercent: -14,
      x:0,
      yPercent: -41,
      scale: 0.165,
      duration: 1,
    });

    this.podTimeline
      .add(postionEndIn)
      .add(animationOut)
      .add(uiOut, '-=0.2')
      .add(foodIn, '-=0.1')
      .add(foodProgress)
      .add(foodOut)
      .add(foodEndIn, '-=0.5')
      .add(foodEndProgress)
      .add(foodEndOut)
      .add(podTopTextIn, '-=0.5')
      .add(gsap.set($('.lounge .tooltip.-mouth'), {className: "+=-active"}))
      .add(gsap.to(foodIn, {duration: 0.3}))

    const animationTrigger = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: podTrigger,
      animation: this.podTimeline,
      scrub: 0.5,
      start: "top bottom",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: false,
      pinSpacing: false,
      markers: false,
    });
  }

  this.initMobile = () => {
    $('.lounge').append($('[data-lounge-pod-text]'));
  }

  this.init = () => {

    this.initAnimation();
    if(isPortrait) this.initMobile();
    
    let currentTooltip = '';

    const animationTrigger = ScrollTrigger.create({
      scroller: "[data-scroll-container]",
      trigger: this.element,
      scrub: 1,
      start: "top top",
      end: "bottom bottom",
      // onUpdate: self => console.log("progress", self.progress),
      pin: this.element.querySelector("[data-snap-animation]"),
      pinSpacing: false,
      markers: false,
    });

    this.snap = new Snap({
      element: this.element,
      trigger: this.snapTrigger,
      forceStart: true,
      mobileOffset: -60,
      goToPoint: (point, duration, index, pointElem, direction, delay) => {

        if(index == 1 || (index == 7 && isMobile) ) {
          $(this.scrollArrow).addClass('-active');
          this.scrollArrowAnimation.play();
        } else {
          $(this.scrollArrow).removeClass('-active');
          this.scrollArrowAnimation.stop();
        }

        if(index == 1) {
          document.querySelector('.btn-menu-toggle').classList.remove('-blue');
        } else {
          document.querySelector('.btn-menu-toggle').classList.add('-blue');
        }

        let tooltip = $(pointElem).data('tooltip');

        $('.text-box').removeClass('is-active');
        $('[data-lounge-pod-text]').removeClass('is-active');
        
        if(currentTooltip != tooltip) { 
          $('.lounge .tooltip').removeClass('-active');
        }

        currentTooltip = tooltip;

        console.log('delay',delay)

        if(index == 6 && !imagesPreloaded) {
          imagesPreloaded = true;

          const preloadImage = src => 
            new Promise(r => {
              const image = new Image()
              image.onload = r
              image.onerror = r
              image.src = src
            })

          // Preload an image
          preloadImage('https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a2/gallery/SKL-03.jpg')
          preloadImage('https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a2/gallery/SKL-04.jpg')
          preloadImage('https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a2/gallery/SKL-05.jpg')
          preloadImage('https://d1ipt3xviiyi7q.cloudfront.net/sia/images/a2/gallery/SKL-06.jpg')

          $('.swiper .gallery__img').each(function(i, el) {
            $(el).css('background-image', `url(${$(el).data('bg')})`);
          })
        }

        if(point) { //(!isMobile && )
          console.log('index', index);

          const delayAnim = index == 1 ? delay : 0;
          console.log('delayAnim', delayAnim)

          setTimeout(()=> {
            if(index == 0 && direction == 'up') {
              gsap.set(document.querySelector('.lounge-animation canvas'), { opacity:1 });
              gsap.set(document.querySelector('#lounge-svg'), { opacity:0 });
            } 
            
            if(index == 2 && direction == 'down') {
              gsap.set(document.querySelector('.lounge-animation canvas'), { opacity:0 });
              gsap.set(document.querySelector('#lounge-svg'), { opacity:1 });
            }

            gsap.to(this.playhead, {
              frame: point,
              duration: duration == 1 ? 0.5 : duration,
              ease: 'none',
              onUpdate: () => this.animation.goToAndStop(this.playhead.frame, true),
              onComplete: () => {}
            })

            if (index < 2) {
              this.video.goToPoint(this.videoPoints[index], duration);
            }
          }, delayAnim)

        }

        setTimeout(()=>{
          if(index == 7) $('[data-lounge-pod-text]').addClass('is-active');

          $('.text-box').eq(index).addClass('is-active');
          if(tooltip) $('.lounge .tooltip'+tooltip).addClass('-active');
        }, (duration == 1 ? 500 : duration*1000-300)+(index == 0 ? delay : 0))
      },
      hasPin: true
    });

    this.snap.init();
  };
}

export default function () {
  document.querySelectorAll("[data-lounge]").forEach((elem) => {
    let lounge = new Lounge({ element: elem });

    lounge.init();
  });
}