import $ from 'jquery';
import utils from '../utils';
import gsap from "gsap";
let { isTouchDevice, onSwipe, Wheel, isMobile } = utils;


function Slider($slider, startFrom){
    var isTouch = true;

    var isAvailable = true,
        $sliders = $slider;

    this.index = startFrom;
    this.sliderLength = $('li', $slider).length;
    this.isNav = $slider.hasClass('captions-list');

    // console.log(this.isNav);

    // $('.gallery__arrow.-right').on('click', function(e) {
    //     nextSlide();
    // })

    // $('.gallery__arrow.-left').on('click', function(e) {
        
    // })

    this.init = function() {

        //refactor
        /*if(this.isNav) {
            this.index = 1;
            $('li', $slider).eq(1).addClass('is-active');
            $('li', $slider).eq(1-1).addClass('is-prev');
            $('li', $slider).eq(1+1).addClass('is-next');
            return;
        }*/

        if(this.index == 3) {
            console.log('show not show this');
            this.index = 2
        }

        $('li', $slider).eq(this.index).addClass('is-active');

        if(this.index == 0) {
            $('li', $slider).eq(this.sliderLength-1).addClass('is-prev');
        } else {
            $('li', $slider).eq(this.index-1).addClass('is-prev');
        }

        if(this.index == this.sliderLength-1) {
            $('li', $slider).eq(0).addClass('is-next');
        } else {
            $('li', $slider).eq(this.index+1).addClass('is-next');
        }
    }

    this.prevSlide = function() {
        var self = this;

        if (isAvailable) {

            this.index == 0 ? this.index = this.sliderLength-1 : this.index--,
            isAvailable = false;

            $sliders.each(function(i, el){
                var $sliderItems = $(this).find('li');
                var index = self.index;

                $sliderItems.removeClass('is-out is-prev is-next');
                $sliderItems.filter('.is-active').addClass('is-out').removeClass('is-active');
                $sliderItems.eq(index).addClass('is-active');

                $sliderItems.eq(index == 0 ? $sliderItems.length-1 : index-1).addClass('is-prev');
                $sliderItems.eq(index == $sliderItems.length-1 ? 0 : index+1).addClass('is-next');

                // $('.gallery__content-list li').removeClass('active').eq(index).addClass('active');

                setTimeout(function() {
                    isAvailable = true,
                    $sliderItems.filter('.is-out').removeClass('is-out');
                    // $('.current-slide').text(index+1);
                }, 1e3)
            })
        }
    }

    this.nextSlide = function() {
        var self = this;

        if (isAvailable) {

            this.index < this.sliderLength - 1 ? this.index++ : this.index = 0,
            isAvailable = false;

            $sliders.each(function(i, el){
                var $sliderItems = $(this).find('li');
                var index = self.index;

                $sliderItems.removeClass('is-out is-prev is-next');
                $sliderItems.filter('.is-active').addClass('is-out').removeClass('is-active');
                $sliderItems.eq(index).addClass('is-active')

                $sliderItems.eq(index == 0 ? $sliderItems.length-1 : index-1).addClass('is-prev');
                $sliderItems.eq(index == $sliderItems.length-1 ? 0 : index+1).addClass('is-next');

                // $('.gallery__content-list li').removeClass('active').eq(index).addClass('active');

                setTimeout(function() {
                    isAvailable = true,
                    $sliderItems.filter('.is-out').removeClass('is-out');
                    // $('.current-slide').text(index+1);
                }, 1e3)
            })
        }
    }

    // var autoplay = setInterval(function(){
        // nextSlide();
    // }, 5000)
}

function SlidersController($wrap) {
    this.$wrap = $wrap;
    this.$sliders = $('.gallery-slider', $wrap)
    this.instances = [];
    this.length = this.$sliders.eq(0).find('li').length;
    this.intervalTime = 3500;

    this.init = function(){
        var self = this;

        var startCorrection = self.$sliders.length - 1;

        this.$sliders.each(function(index, el) {
            var slider = new Slider($(el), index);
            slider.init();

            self.instances.push(slider)
        });

        var navSlider = new Slider($('.captions-list', $wrap), 0);
        navSlider.init();
        self.instances.push(navSlider);

        this.updateCounter();
        this.initInterval();
        this.attachEvents();
    }

    this.getIndex = function() {
        return this.instances[1].index;
    }

    this.next = function(){
        this.instances.forEach(function(el, index) {
            el.nextSlide()
        });

        this.updateCounter();
        this.resetInterval();
    }

    this.prev = function(){
        this.instances.forEach(function(el, index) {
            el.prevSlide()
        });

        this.updateCounter();
        this.resetInterval();
    }

    this.updateCounter = function(){
        var index = this.getIndex();

        $('.counter_current', this.$wrap).text('0'+(index+1));
        $('.counter_total', this.$wrap).text('0'+this.length);

        gsap.fromTo($('.counter .counter_line span', this.$wrap)[0], {
            xPercent: -100
        }, {
            duration: (this.intervalTime-300)/1000,
            xPercent: 0,
            ease: 'none',
            delay:1,
        })
    }

    this.initInterval = function() {
        var self = this;
        this.interval = setInterval(function() {
            if(self.$wrap.hasClass('is-inview')) self.next();
        }, this.intervalTime)
    }

    this.resetInterval = function() {
        this.interval = clearInterval(this.interval);
        this.initInterval();
    }

    this.attachEvents = function() {
        var self = this;

        if(!isTouchDevice) {
            this.$wrap.click(function(e){
                if(e.clientX > self.$wrap.offset().left+self.$wrap.width()/2) {
                    self.next();
                } else {
                    self.prev();
                }
            })
        }

        if(isTouchDevice) {
            $('.slider-cursor', this.$wrap).click(function(e){
                if($(this).hasClass('left')) {
                    self.prev();
                } else {
                    self.next();
                }
            })

            var handleSwipe = new onSwipe(this.$wrap[0], {
                onLeft: function(){
                    self.next();
                },
                onRight: function(){
                    self.prev();
                }
            });
        }
    }
}

export default function () {
    $('[data-slider]').each(function(index, el) {
        var slider = new SlidersController($(el));

        slider.init();
        window.dispatchEvent(new Event('resize'));
    });
}